import React from 'react';
require('../assets/css/main.css');
import Header from "./Header.js";
import Content from "./Content.js";
import Theme from '../assets/js/theme.js';

class App extends React.Component {
  ScrollDetection(){
    window.onscroll = function() {
      var scrollTop = (((window.pageYOffset || document.scrollTop)  - (document.clientTop || 0)) || 0);
      var header = document.querySelector('.header');
      var targetPoint = header.getBoundingClientRect().height - 100;
      var themedBackdrop = document.querySelector('backdrop.themed');
      if(scrollTop < targetPoint){
        var colorOpacity = scrollTop/targetPoint;
        themedBackdrop.style.opacity = colorOpacity;
        try {
          header.classList.remove('color-change');
        } catch (e) {

        }
      }
      else if (scrollTop >= targetPoint) {
        header.classList.add('color-change');
        themedBackdrop.style.opacity = '1';
      }
      if(scrollTop < (targetPoint + 100)){
        try {
          header.classList.remove('shadow-change');
        } catch (e) {

        }
      }
      else if(scrollTop >= (targetPoint + 100)){
        header.classList.add('shadow-change')
      }

    };
  }
   render() {
     this.ScrollDetection();
      return (
        <div className="app">
          <Header />
          <Content />
        </div>
      );
   }
}

export default App;



