import React from "react";

class Behance extends React.Component {
  constructor() {
    super();
  }
  loadBehance(behanceRef){
    var xhttp = new XMLHttpRequest();
    xhttp.onreadystatechange = function() {
      if (this.readyState == 4 && this.status == 200) {
       return this.responseText;
      }
    };
    xhttp.open("GET", behanceRef, true);
    xhttp.send();
  }
  render(){
    console.log(this.loadBehance("https://www.behance.net/v2/users/GeekyBrowny/projects?api_key=SosvyxkURuZuc85aO8kDxKeXycK3RIJV"));
    return (
      <div className="behance-section">
        <h1>Behance</h1>
      </div>
    );
  }
}
export default Behance;



