import React from 'react';
import Bio from './Bio';
import Projects from "./Projects"
require('../assets/css/content.css');

class Content extends React.Component {
   render() {
      return (
        <div id="content" className="content">
          <Bio />
          <Projects />
        </div>  
      );
   }
}

export default Content;



