import React from 'react';
require('../assets/css/header.css');

class Header extends React.Component {
   render() {
      return (
        <div className="header">
          <backdrop className="themed"></backdrop>
          <backdrop></backdrop>
          <div className="navbar">
             <div className="tools">
               <div className="brand">
                 OSK
               </div>
               <div className="stick-right">
                 <a>Bio</a>
                 <a>Projects</a>
                 <a>Blog</a>
                 <a>Contact</a>
               </div>
             </div>
          </div>
          <div className="sentence">
            <h3 className="say">Hi, I am</h3>
            <h3 className="name">Omkar Kulkarni!</h3>
          </div>
        </div>
      );
   }
}

export default Header;



