import React from 'react';
require('../assets/css/section.css');

class Bio extends React.Component {
   render() {
      return (
        <div className="section bio">
          <h2 className="section-heading">Bio</h2>
          <div className="section-body">
            <div className="bio-text">
              <p>I am a beginner front end developer and designer living in India,
                passionate about using technology as an impression to express creative minds.
                Front End Development is something which I love, worship and adore.
                I design a bit too. I love to make lighthearted, humorous artworks depicting a personality or an idea.
                I love Films. The idea of telling a story through a visual medium sounds like home to me...
                Most of the time I draw my inspirations for design through Films and Music. I also have a love for making those but that way right now is a little uncharted but soon to be familiar to me...
                By the way, Coffee is something which I always love to keep on my table. In the same way, a notepad for doodling and some chips to get fat.
              </p>
            </div>
          </div>
        </div>
      );
   }
}

export default Bio;



